.ifs-map-container {
  position: relative;
  width: inherit;
  height: inherit; }
  .ifs-map-container > .ifs-geo-search {
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;
    border-bottom: 1px solid #c8c8c8; }
    .ifs-map-container > .ifs-geo-search > input[type="text"] {
      width: calc(100% - 42px);
      height: 30px;
      line-height: 30px;
      margin-left: 20px;
      padding: 0 10px;
      border: 1px solid #c8c8c8;
      outline: 0;
      font-size: 14px;
      color: #000; }
  .ifs-map-container > .ifs-map-filter-button {
    position: absolute;
    bottom: 23px;
    right: 60px;
    z-index: 1; }
  .ifs-map-container > .ifs-map-legend {
    position: absolute;
    bottom: 2px;
    left: 0;
    padding: 5px;
    z-index: 1;
    background: white;
    font-size: 0.775rem;
    border-radius: 4px; }
    .ifs-map-container > .ifs-map-legend > div {
      margin-bottom: 5px; }
    .ifs-map-container > .ifs-map-legend > ul {
      margin: 0;
      padding: 0;
      list-style-type: none; }
      .ifs-map-container > .ifs-map-legend > ul > li {
        display: flex;
        align-items: center; }
      .ifs-map-container > .ifs-map-legend > ul > li:before {
        content: "";
        width: 10px;
        height: 10px;
        margin-right: 10px;
        border-radius: 50%; }
      .ifs-map-container > .ifs-map-legend > ul > li.ifs-min:before {
        background-color: #f4e541; }
      .ifs-map-container > .ifs-map-legend > ul > li.ifs-med:before {
        background-color: #f4a341; }
      .ifs-map-container > .ifs-map-legend > ul > li.ifs-max:before {
        background-color: #f44242; }
  .ifs-map-container > .ifs-map-wrapper {
    width: 100%;
    height: calc(100% - 40px); }
